.ul {
  position: absolute;
  padding: 0;
  margin: 0;
  bottom: -40px;
  width: 100vw;
  left: -70px;
  list-style: none;
}
.ul a {
  font-size: 12px;
  font-weight: lighter;
  color: #ffffff;
  text-decoration: none;
}
.ul a:hover, .ul a:focus, .ul a:active {
  color: #ffffff;
  text-decoration: none;
  outline: none;
}
.li:not(:last-child) {
  margin-bottom: 30px;
}
@media (min-width: 1200px) {
  .ul {
    left: 0;
    bottom: -20px;
  }
  .ul a {
    font-size: 24px;
    transition: all .3s ease;
  }
  .ul a:hover {
    color: #69c9ca;
  }
  .li:not(:last-child) {
    margin-bottom: 2vh;
  }
}
