.navBar {
  position: relative;
}
.img {
  display: block;
  height: 68px;
  width: auto;
  user-select: none;
}
.navBarNotTransparent {
  position: relative;
  z-index: 999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background: #000000;
}
.mediaMenu {
  display: flex;
  align-items: center;
  color: #ffffff;
  font-size: 24px;
  font-family: "Caviar Dreams";
}
.mediaMenu .span {
  margin-right: 25px;
  user-select: none;
}
@media(min-width: 1200px) {
  .navBarNotTransparent {
    padding: 40px 40px 0 100px;
  }
}
