.navsWrapper {
  position: absolute;
  left: 0;
  z-index: 998;
  background: #000000;
  height: 512px;
  width: 100%;
}
.nav {
  float: right;
  position: relative;
}
.navs {
  user-select: none;
  display: inline-flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style: none;
  margin-right: 30px;
}
.link {
  font-size: 22px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background: none;
  border: none;
  padding: 0;
  transition: all 0.5s ease-out;
  font-family: "Caviar Dreams"
}
.link:first-child {
  display: flex;
  align-items: center;
}
.i {
  font-size: 30px;
  color: #ffffff;
  transition: all .2s ease-in-out;
}
.iRotate {
  transform: rotate(180deg);
  color: #69c9ca;
}
.linkActive {
  color: #69c9ca;
}
.link:active, .link:focus {
  outline: none;
}
.listItem:not(:last-child) {
  margin-bottom: 50px;
}
.contacts {
  position: absolute;
  bottom: 0;
  color: #ffffff;
  text-align: center;
  width: 100%;
  font-size: 14px;
  font-weight: lighter;
  user-select: none;
}
.contacts p {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contacts i {
  color: #69c9ca;
  margin-right: 10px;
}
@media(min-width: 1200px) {
  .navsWrapper {
    height: calc(100vh - 157px);
    background:#000000;
    background-size: 100% auto;
    background-position: 50% 30%;
  }
  .nav {
    margin-top: 3vh;
  }
  .navs {
    margin-right: 175px;
  }
  .navs button {
    cursor: pointer;
  }
  .link {
    font-size: 48px;
  }
  .link:hover {
    color: #69c9ca;
  }
  .listItem:not(:last-child) {
    margin-bottom: 3vh;
  }
  .i {
    font-size: 48px;
  }
