.wrapper {
  position: absolute;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  top: 50%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
}
.wrapper h1, .wrapper p, .wrapper a {
  text-align: center;
  display: inline-block;
  font-family: "Caviar Dreams";
  user-select: none;
}
.wrapper h1 {
  color: #ffffff;
  font-size: 38px;
  margin: 0;
  line-height: 24px;
  margin-bottom: 50px;
}
.wrapper span {
  font-size: 18px;
  color: #69c9ca;
  font-weight: bold;
  padding-left: 265px;
  line-height: 18px;
}
.wrapper .lastOne {
  font-size: 22px;
  color: #ffffff;
  line-height: 2;
  margin: 0 0 35px 0;
}
.wrapper a {
  padding: 15px 20px;
  border: 1px solid #69c9ca;
  color: #69c9ca;
  text-decoration: none;
  font-family: "Open Sans";
  font-weight: lighter;
  font-size: 14px;
}
.wrapper a:hover, .wrapper a:focus, .wrapper a:active {
  outline: none;
  text-decoration: none;
  color: #69c9ca;
}

@media(min-width: 1200px) {
  .wrapper h1 {
    font-size: 100px;
    line-height: 45px;
    margin-bottom: 3vh;
  }
  .wrapper span {
    font-size: 36px;
    padding-left: 730px;
  }
  .wrapper .lastOne {
    font-size: 48px;
    color: #ffffff;
    line-height: 1.5;
    margin: 0 0 5vh 0;
  }
  .wrapper a {
    font-size: 18px;
    position: absolute;
    bottom: -120px;
    left: 50%;
    transform: translate(-50%, 0);
    transition: all 0.3s ease;
  }
  .wrapper a:hover {
    color: #ffffff;
    background: #69c9ca;
  }
}
