.container {
  background: linear-gradient(to top, #000000, #000000, #022539, #000000, #000000);
  height: 620px;
  width: 100%;
  position: relative;
}
.triangle {
  width: 0;
  height: 0;
  position: absolute;
  border-left: 50vw solid transparent;
  border-right: 50vw solid transparent;
  border-top: 20px solid #000000;
  bottom: -20px;
  z-index: 999;
}
.particles {
  position: absolute;
  z-index: 1;
  top: 108;
  left: 0;
}
@media(min-width: 1200px) {
  .container {
    height: calc(100vh - 50px);
    background: linear-gradient(to top, #000000, #022539, #000713, #000000);
  }
  .triangle {
    bottom: -50px;
    border-top: 50px solid #000000;
  }
  .particles {
    top: 120px;
  }
}
